input:focus-visible,
textarea:focus-visible {
  outline: none;
}

/* https://stackoverflow.com/a/22306944/13151903 */
input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0;
}

input[type='color']::-webkit-color-swatch-wrapper {
  padding: 0;
}
input[type='color']::-webkit-color-swatch {
  --tw-border-opacity: 1;
  border-color: rgb(var(--md-sys-color-outline-variant) / var(--tw-border-opacity));
}

.scroll {
  overflow: overlay;
}
.scroll::-webkit-scrollbar {
  width: 10px;
}
.scroll:hover::-webkit-scrollbar-thumb {
  background-color: rgb(var(--md-sys-color-on-background) / 0.1);
}
.scroll:hover::-webkit-scrollbar-thumb:hover {
  background-color: rgb(var(--md-sys-color-on-background) / 0.2);
}
.scroll:hover::-webkit-scrollbar-thumb:active {
  background-color: rgb(var(--md-sys-color-on-background) / 0.3);
}
.scroll-parent {
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.scroll-h {
  overflow-x: auto;
}
.scroll-h::-webkit-scrollbar {
  display: none; /* Safari and Chrome */
}

.match-highlight {
  background-color: rgb(234 179 8 / 0.3);
  color: unset;
}

p {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

/* https://daisyui.com/components/divider */
.divider {
  display: flex;
  flex-direction: row;
  align-items: center;
  align-self: stretch;
  height: 1rem;
  white-space: nowrap;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  gap: 0.5rem;
}
.divider:before,
.divider:after {
  content: '';
  flex-grow: 1;
  width: 100%;
  height: 1px;
  --tw-bg-opacity: 1;
  background-color: rgb(var(--md-sys-color-surface-variant) / var(--tw-bg-opacity));
}

.progress-bit {
  animation-name: progress;
  animation-duration: 4s;
  animation-iteration-count: infinite;
  transform: translate3d(0px, 0px, 0px);
  animation-timing-function: linear;
}

@keyframes progress {
  from {
    transform: translateX(0%) scaleX(1);
  }
  50% {
    transform: translateX(1000%) scaleX(3);
  }
  to {
    transform: translateX(2000%) scaleX(1);
  }
}

.list-row:hover .action {
  display: block;
}

.placeholder\:text-outline\/60::-moz-placeholder {
  color: rgb(var(--md-sys-color-outline) / 0.6);
}

.placeholder\:text-outline\/60::placeholder {
  color: rgb(var(--md-sys-color-outline) / 0.6);
}

.hover\:bg-surface:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(var(--md-sys-color-surface) / var(--tw-bg-opacity));
}

.hover\:bg-on-surface\/hover:hover {
  background-color: rgb(var(--md-sys-color-on-surface) / var(--md-sys-state-hover-state-layer-opacity));
}

.hover\:text-on-surface-variant:hover {
  --tw-text-opacity: 1;
  color: rgb(var(--md-sys-color-on-surface-variant) / var(--tw-text-opacity));
}

.hover\:text-on-surface:hover {
  --tw-text-opacity: 1;
  color: rgb(var(--md-sys-color-on-surface) / var(--tw-text-opacity));
}

.hover\:bg-surface1:hover {
  background-image: linear-gradient(rgb(var(--md-sys-color-primary) / 0.05),rgb(var(--md-sys-color-primary) / 0.05));
  background-color: rgb(var(--md-sys-color-surface));
}

.hover\:bg-surface2:hover {
  background-image: linear-gradient(rgb(var(--md-sys-color-primary) / 0.08),rgb(var(--md-sys-color-primary) / 0.08));
  background-color: rgb(var(--md-sys-color-surface));
}

.hover\:bg-surface3:hover {
  background-image: linear-gradient(rgb(var(--md-sys-color-primary) / 0.11),rgb(var(--md-sys-color-primary) / 0.11));
  background-color: rgb(var(--md-sys-color-surface));
}

.hover\:bg-surface4:hover {
  background-image: linear-gradient(rgb(var(--md-sys-color-primary) / 0.12),rgb(var(--md-sys-color-primary) / 0.12));
  background-color: rgb(var(--md-sys-color-surface));
}

.hover\:bg-surface5:hover {
  background-image: linear-gradient(rgb(var(--md-sys-color-primary) / 0.14),rgb(var(--md-sys-color-primary) / 0.14));
  background-color: rgb(var(--md-sys-color-surface));
}

.focus\:outline-none:focus {
  outline: 2px solid transparent;
  outline-offset: 2px;
}

.disabled\:bg-disabled:disabled {
  background-color: rgb(var(--md-sys-color-on-surface) / 0.12);
}

.disabled\:text-on-disabled:disabled {
  color: rgb(var(--md-sys-color-on-surface) / 0.38);
}

.disabled\:ring-disabled:disabled {
  --tw-ring-color: rgb(var(--md-sys-color-on-surface) / 0.12);
}

.group:hover .group-hover\:visible {
  visibility: visible;
}

.group:hover .group-hover\:border-on-surface {
  --tw-border-opacity: 1;
  border-color: rgb(var(--md-sys-color-on-surface) / var(--tw-border-opacity));
}

.peer:checked ~ .peer-checked\:visible {
  visibility: visible;
}

.dark .dark\:inline-block {
  display: inline-block;
}

.dark .dark\:hidden {
  display: none;
}

@media (min-width: 640px) {

  .sm\:sticky {
    position: sticky;
  }

  .sm\:mb-0 {
    margin-bottom: 0px;
  }

  .sm\:block {
    display: block;
  }

  .sm\:flex {
    display: flex;
  }

  .sm\:flex-initial {
    flex: 0 1 auto;
  }

  .sm\:flex-col {
    flex-direction: column;
  }

  .sm\:px-2 {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
  }
}

@media (min-width: 1024px) {

  .lg\:typescale-body-medium {
    font-family: var(--md-sys-typescale-body-medium-family);
    line-height: var(--md-sys-typescale-body-medium-line-height);
    font-size: var(--md-sys-typescale-body-medium-size);
    letter-spacing: var(--md-sys-typescale-body-medium-tracking);
    font-weight: var(--md-sys-typescale-body-medium-weight);
  }
}

